import React from 'react';
import { BrowserRouter, Route } from 'react-router-dom';
import List from './List';
import ViewPerson from './ViewPerson';

const Rout = ({ tenants }) => (
    <BrowserRouter>
        <Route exact path="/" component={() => <List tenants={tenants} /> } />
        <Route path="/:user/:userid" component={ViewPerson} />
    </BrowserRouter>
);

export default Rout;