import React from 'react';
import Item from './Item';

const List = ({ tenants }) => (
<div id="list">
    <h2>רשימת דיירים</h2>
    <hr />
    {tenants.map(item => {
        return (
            <Item
                key={item.id} 
                id={item.id}
                fullname={item.fullname}
                building={item.building}
                apartment={item.apartment}
                cellphone={item.cellphone}
                phone={item.phone}
            />
        )
    })}
</div>
);

export default List;